
    // reservation
        .reservation {
            padding-top: 48px;
            padding-bottom: 88px;
            @include table {
                padding-top: 64px;
                padding-bottom: 80px;
            }
            @include pc {
                padding-top: 64px;
                padding-bottom: 104px;
            }
        }

    // reservation done
        .check-circle {
            min-width: 48px;
            min-height: 48px;
            margin-bottom: 20px;
            background-image: url($bgPath+$check-circle);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
        }
        .arrow-down {
            min-width: 16px;
            min-height: 9.88px;
            background-image: url($bgPath+$arrow-dow);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: 50% 50%;
        }
        .sort-down-area {
            width: 40px;
            height: 40px;
            padding: 16.67px 11.67px 15px;
        }
        .sort-down {
            width: 16.67;
            height: 8.33px;
            background-image: url($bgPath+$sort-down);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: 50% 50%;
        }
        .check-circle-white {
            min-width: 36px;
            min-height: 36px;
            margin-bottom: px;
            background-image: url($bgPath+$check-circle-white);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
        }
        .check-circle-Alto {
            min-width: 36px;
            min-height: 36px;
